import axios from "axios";

export const EXPERTSES_URL = "StockExpertises";
export const getAll=()=> axios.get(`${EXPERTSES_URL}/GetAll`);
export const getStockExpertiseById=(stockExpertiseId) =>axios.get(`${EXPERTSES_URL}/GetById?id=${stockExpertiseId}`);
export const getStockExpertiseByStock=(stockId) =>axios.get(`${EXPERTSES_URL}/GetByStock?stockId=${stockId}`);
export const createStockExpertise=(stockExpertise) =>axios.post(`${EXPERTSES_URL}/Insert`, stockExpertise);
export const updateStockExpertise=(stockExpertise) =>axios.put(`${EXPERTSES_URL}/Update`, stockExpertise);
export const deleteStockExpertise=(stockExpertiseId) =>axios.delete(`${EXPERTSES_URL}/Delete?id=${stockExpertiseId}`);

